import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import CartList from './CartList'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import cartActionCreators from '../actions/cartIndex'

class Cart extends Component{

     render(){

        return(

             <div> 
                 Welcome to your cart
                 <section className="list">
                <ol>
                    Your cart items:
                    
                </ol>
            </section>
             </div>


        )

     }

}

function mapStateToProps(storeState){
  
    console.log('inside mapStatetoprops products index.js',storeState.products);
     return { data: storeState.products};
 
 }
 
 
 function mapDispatchToProps(dispatch) {
     console.log('we are here mapDispatchToProps',dispatch);
     return bindActionCreators(cartActionCreators,dispatch);
 }

 export default connect(mapStateToProps, mapDispatchToProps)(Cart);